import colors from '../colors'
import posed from "react-pose";

const Square = posed.div({
  idle: { scale: 1 },
  hovered: { scale: 1.05 }
});

class Member extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      h: false,
    }
  }

  render() {
    return(
      <Square
        pose={this.state.hTeam ? "hovered" : "idle"}
        onMouseEnter={() => this.setState({ h: true })}
        onMouseLeave={() => this.setState({ h: false })}>
        {(this.state.h ? (
          <div className="has-text-white has-text-centered is-flex" style={{flexDirection: "column", justifyContent: "center", height: 210, width: 210, borderRadius: 105, backgroundColor: colors.b4}}>
            <p className="title is-size-5"> <b> Eva Killenberg </b> </p>
            <p className="subtitle is-size-6"> Sophomore {'/n'} Little Compton, Rhode Island Computer Science </p>

          </div>) : (
          <div style={{height: 200, width: 200, borderRadius: 100, backgroundColor: colors.b3}}>
          </div>))}
      </Square>
    );
  }
}

export default Member;
